export default {
	data() {
		return {
			CustomBar: this.CustomBar,
			xueya: {
				min: 100,
				max: 100
			},
			xuetang: '',
			zclist: [
				{
					color: 'text-orange',
					name: '血糖自测',
					type: 1
				},
				{
					color: 'text-blue',
					name: '血压自测',
					type: 2
				},
				{
					color: 'text-green',
					name: '心率自测',
					type: 3
				}
			]
		};
	},
	methods: {
		tozice() {
			uni.navigateTo({
				url: '../zice/zice?type=1'
			});
		},
		tuadd(){
			uni.navigateTo({
				url: '../add/add'
			});
		}
	}
};
